.headertop{
    
    background-color: #ECECEC;
}

.headertop > .headertop__items{
    /* width: 1188px; */
    display: flex;
    margin-left: 10%;
    justify-content: center;
    
}

.headertop__items > p{
    margin-left: 2rem;
    font-size: 12px;
    font-weight: 400;
    line-height: 25px;
    color: #323332;
}

.headertop > .headertop__items > .headertop__savemore{
    color: rgb(243, 111, 54);
}

.headertop__items  a{
    text-decoration: none;
}

a > p{
    margin-left: 1rem;
    font-size: 12px;
    font-weight: 400;
    line-height: 25px;
    color: #323332;
}

@media screen and (max-width: 600px) {

  .headertop{
      display: none;
  }
  
  
  }