.feature{

    margin-top: 10px;
    margin-bottom: 30px;
}

.feature .feature__buttons{
    display: flex;
    justify-content: center;
}

.feature__buttons{
    margin-left: 0.1%;
}

.feature .MuiButton-root {
    margin-left: 20px;
}

.MuiButton-root p{
    margin-left: 5px;
}

 .MuiButton-root img{
    width: 32px;
    height: 32px;
    /* margin-right: 30px; */
}

.MuiButton-root {
    display: flex !important;
   align-items: center !important;
    background-color: white !important;
    margin-top: 10px !important;
    margin-bottom: 10px !important;
    /* margin-left: 10px !important; */
    width: 220px !important;
    border-radius: 50px !important;
   justify-content: flex-start !important;
}

.feature__items{
    display: none;
}

@media screen and (max-width: 600px) {

    .feature .feature__buttons{
        display: flex;
        flex-wrap: wrap;
    }

    .feature__buttons > .MuiButton-root {
        display: none !important;
    }

    .feature__items{
        display: flex;
        flex-wrap: wrap;
        margin: 10px;
    }

    .feature__item{
        margin: 10px;
    }

    .feature__item > p{
        font-size: 10px;
    }

    .feature__items img{
        width: 35px;
        height: 35px;
        /* margin-right: 30px; */
      margin-left: 10px;
    }

    
  
  }