.header{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 75px;
    width: 100%;
    background-color: white;
    /* width: 1188px; */
}

.header > a{
    text-decoration: none;
    color: white;
}

.header a > .header__logoContent > .header__logo{
    width: 135px;
    height: 41;
    /* margin-right: 40px; */
}

.header__logoContent{
    width: 200px;
    height: 39px;
}

.header > .header__appDownload{
    width: 188px;
    height: 45px;
    margin-left: 35px;
}

.header__search{
    display: flex;
    align-items: center;
    width: 690px;
    height: 50px;
    /* border: 1px solid black; */
    background-color: #F5F5F5;
}

.header__input{

    border: none;
    width: 640px;
    /* height: 47px; */
    outline: none;
    padding: 13px 9px;
    font-size: 14px;
    color: #212121;
    box-shadow: none;
    border-radius: 2px;
    background-color: #F5F5F5;
    
}

.header__shoppingCart{
    margin-left: 20px;
}

.header__cart{
    display: flex;
    align-items: center;
}

.header__cartUpdate{
    width: 20px;
    height: 20px;
    background-color: chocolate;
    border-radius: 100%;
    position: absolute;
    background: #f36e36;
    margin-left: 40px;
    margin-bottom: 20px;
}

.header__cartUpdate > p{
    margin-left: 6px;
    margin-bottom: 10px;
}

@media screen and (max-width: 600px) {
    .header__input, .header__search{
    
        max-width: 250px;
        border-radius: 50px !important;
        height: 25px;
        
    }
   

    .header > .header__appDownload{
       display: none;
    }

    .header__search {
        display: flex;
        align-items: center;
        width: 250px;
        height: 25px;
        /* border: 1px solid black; */
        background-color: #F5F5F5;
    
       
    }

      .inputIcon {
        background-color: transparent !important;
        padding: 0px !important;
        height: 42px !important;
        color: black !important;
        border-radius: 29px !important;
        position: absolute !important;
        margin-left: 10px !important;
        
     
    }

    .inputIcon{
        display: none !important;
    }

    .header{
        display: flex;
        align-items: center;
        justify-content: center;
        /* height: 75px;
        width: 100%; */
        width: 100%;
        background-color: white;
        margin-bottom: 25px;
        margin: 0px;
    }
    .header a > .header__logoContent > .header__logo{
        width: 60px;
        height: 31px;
        /* margin-right: 40px; */
    }

    .header__logoContent {

        width: 70px;
        height: 41px;
    }
 
  }