.slider {
    height: 345px;
    background-color: #382F88;
    /* margin-top: 5px; */
    z-index: -1;

}

.slider > img{
    margin: auto;
}

.slick-slide img{

    /* margin: auto; */
  margin-left: 29%;
    height: 345px;
    object-fit: contain;
    /* z-index: 100; */
}

.slider__productCat{
    background-color: white;
    color: #757575;
    white-space: nowrap;
    width: 199px;
    height: auto;
    z-index: 100;
    position: absolute;
    margin-left: 19%;
    padding: 8px 0 12px;
}

.slider__productCat > p{
    font-size: 13px;
    /* padding-top: 1px; */
    padding-left: 6px;
    line-height: 27px;
    cursor: pointer;
    overflow: hidden;
}