.singleproduct{
    background-color: white;
    display: flex;
    margin: auto;
    margin-top: 10px;
    align-content: center;
    width: 1100px;
    height: max-content;
    padding: 20px;
}

.singleproduct > img {
    width: 400px;
    height: 400px;
}

    .singleproduct__info  p{
    color: #212121;
    font-size: 22px;
    font-weight: normal;
    word-break: break-word;
    word-wrap: break-word;
    overflow-wrap: break-word;
    margin-bottom: 3rem;
    margin-left: 3rem;
}

 .singleproduct__price  {
    font-size: 30px !important;
    color: #f57224 !important;
}

.singleproduct__buttons{
    display: flex;
    align-content: center;
    align-items: center;
    margin-left: 20px;
    
}

.singleproduct__info > .singleproduct__buttons > .MuiButton-root{
 
    border-radius: 0px !important;
    width: 30% !important;
    margin-left: 25px !important;
    height: 44px !important;

}

.MuiButton-label {
    width: 100%;
    display: inherit;
    align-items: inherit;
    justify-content: center !important;
}

 .singleproduct__buttons > .singleproduct__cart  {
    background: #f57224 !important;
    border: 1px solid #f57224  !important;
    color: #fff !important;
    
}

 .singleproduct__buttons > .singleproduct__buy  {
   
    border: 1px solid #2abbe8 !important;
    background: #2abbe8 !important;
    color: #fff !important;
}

@media screen and (max-width: 600px) {
    .singleproduct{
        background-color: white;
        display: flex;
        flex-direction: column;
        margin: auto;
        margin-top: 10px;
        align-content: center;
        width: 550px;
        height: max-content;
        padding: 20px;
    }

    .singleproduct__info  p{
        color: #212121;
        font-size: 18px;
        width: 300px;
        margin-top: 10px;
        font-weight: normal;
        margin-left: 4rem;
    }

    .singleproduct__price  {
        font-size: 20px !important;
        color: #f57224 !important;
    }

    .singleproduct > img {
        width: 200px;
        height: 200px;
       margin-left: 4rem;
     
    }
 
  }


