.product{
    width: 250px;
    height: 350px;
    background: white;
    padding: 10px;
    margin-bottom: 10px;
    margin-left: 10px; 
    /* box-shadow: 0 0 1px;    */
}

.product:hover {
   
  
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    box-shadow:0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1);
}

.product > img {
    height: 233px;
    width: 233px;  
    cursor: pointer;
}

.product__info > p{
    padding-bottom: 7px;
}

p.product__desc{
    font-size: 16px;
    line-height: 24px;
}

.product__descM{
    display: none;
}

.product__desc{
    cursor: pointer;
    font-size: 13px;

font-weight: 400;

line-height: 16px;
}

.product__price{
    font-size: 18px;
    color: #f57224;
    font-weight: 500;
}

@media screen and (max-width: 600px) {
    
    .product{
        width: 100px;
        height: 250px;
        background: white;
        padding: 10px;
        margin-bottom: 10px;
        margin-left: 10px; 
        /* box-shadow: 0 0 1px;    */
    }

    .product > img {
        height: 80px;
        width: 80px;  
        cursor: pointer;
    }

    .product__price{
        font-size: 14px;
        color: #f57224;
        font-weight: 500;
    }

    p.product__desc{
        /* font-size: 12px;
        line-height: 24px; */
        display: none;
    }

    .product__descM{
        display: inline;
        font-size: 12px;

    }

    .product__rating p{
        font-size: 12px;
    }
  }