.cartProduct{
    display: flex;
    align-items: center;
    background-color: white;
    padding: 15px;
    border-top: 1px solid gray;
    flex: 0.8;
    box-shadow:0 1px 4px rgba(0, 0, 0, 0), 0 0 40px rgba(0, 0, 0, 0);
}

.cartProduct > img {
    width: 80px;
    height: 80px;
}

.cartProduct__info{
    display: flex;
    align-items: center;
    margin-left: 10px;
}

.cartProduct__info > p{
    margin-left: 10px;
}

.cartProduct__info > .cartProduct__desc {
   
    max-height: 32px;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-left: 0;
    font-size: 14px;
    color: #212121;
    line-height: 16px;

}

.cartProduct__info > .cartProduct__price {
    font-size: 18px;
    color: #f57224;
    font-weight: 400;
}

.cartProduct__remove{
    margin-left: 10px;
}