.login{
    margin: auto;
    max-width: 810px;
    height: 400px;
    margin-top: 40px;
    /* background-color: #999; */
}

form{
    display: flex;
    padding:  25px 50px 50px 25px;
    align-items: center;
    background-color: white;
   margin-top: 30px;
}

.form__inputFields{
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    width: 70%;
    /* border: 1px solid #ddd; */
    font-size: 14px;
    line-height: 38px ;
    padding: 0 35px 0 10px;
    margin-bottom: 2px;
    outline: none;
}

.form__inputFields > input{
    height: 40px;
    outline: none;
    border:  1px solid #ddd;
    width: 100%;
    margin-bottom: 2px;
}

.form__register{
    background-color: teal;
    padding: 10px;
    margin-top: 10px;
    color: white;
     border: none;
     outline: none;
}

.form__buttons{
    margin-left: 2rem;
    margin-top: 30px;
}

 .logInWithEmailPass {
    border-radius: 3px !important;
