.payment{
    display: flex;
    align-content: center;
    width: 1200px;
    margin: auto;
    margin-top: 2.5rem;
    margin-bottom: 15px;
 
}

.payment__orderSummary{
    flex: 0.3;
    height: 150px;
    background-color: white;
    border: 1px solid white;
    margin-left: 20px;
    padding: 10px;
    box-shadow: 0 0 9px 0 #c2c2c2;
    margin-bottom: 15px;
   
}

.payment__checkout{
        
    flex: 0.7;
    margin-left: 5rem;
    margin-bottom: 15px;
}

.payment__card{
    display: flex;
    flex-direction: column;
   align-items: flex-start;
   padding: 40px;
}

.StripeElement

 {

  margin: 10px 0 20px 0;
  max-width: 500px;
  padding: 10px 14px;
  font-size: 1em;
  font-family: "Source Code Pro", monospace;
  box-shadow: rgba(50, 50, 93, 0.14902) 0px 1px 3px,
    rgba(0, 0, 0, 0.0196078) 0px 1px 0px;
  border: 0;
  outline: 0;
  border-radius: 4px;
  background: white;
}

 .card__number{
    border: none;
    outline: none;
    padding: 0;
    height: 40px;
    font-weight: 400;
    vertical-align: baseline;
    background-color: transparent;
    width: 300px;
    padding: 3px 8px;
}

form > button {
    border-style: solid;
    background-color: #f57224;
    color: white;
    border-color: transparent;
    margin: 0;
    width: 288px;
    height: 48px;
    color: #fff;
    font-size: 16px;
    padding: 0 36px;
    font-size: 14px;
    line-height: 46px;
    border-width: 1px;
}

.cart__Cvc{
    width: 40px;
    margin-left: 20px;
    margin-top: 30px;
}

.card__exp{
    display: flex;
    align-items: center;
}