.cart{
    display: flex;
    margin: auto;
    margin-top: 10px;
    align-content: center;
    width: 1195px;
    height: max-content;
    padding: 20px;
}

.cart__title{
    margin-right: 10px;
    padding: 20px;
    border-bottom: 1px solid lightgray;
    background-color: white;
}

.cart__product{
    margin-top: 20px;
}

@media screen and (max-width: 600px) {
    .cart{
        display: flex;
        flex-direction: column;
       
        margin-top: 10px;
        align-content: center;
        width: 400px;
        height: max-content;
        padding: 20px;
    }

    .cartProduct{
        margin-top: 20px;
     margin-bottom: 20px;
    }
 
  }